            <p class="icon_p  mb24 mt24"><i class="red">*</i>计价产品:</p>
            <div class="option both">
              <p class="select">薄膜面板</p>
              <p>薄膜开关</p>
            </div>
          </div>
        </van-collapse-item>
        <van-collapse-item title="工艺信息" title-class="NewTitle" name="2">
          <div class="basic">
            <!--            尺寸大小-->
            <p class="icon_p"><i class="red">*</i>尺寸大小： <i class="iconfont">&#xe653;</i></p>
            <span class="f24 cl_999 dis mb24">注:尺寸可以精确到2位小数；如与文件尺寸有差异，以文件为准</span>
            <div class="importInput both">
              <input type="text" placeholder="高度/y" v-model="Height" @input="HeightWidth">
              <span>X</span>
              <input type="text" placeholder="宽度/x" v-model="Width" @input="HeightWidth">
              <span>cm</span>
            </div>
            <!--            定制数量：单片(pcs)-->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>定制数量：单片(pcs)<i class="iconfont">&#xe653;</i></p>
            <div class="selectNumber">
              <div class="input">
                <p>请选择</p>
                <img src="../../../assets/img/common/selectbelow.png" alt="">
              </div>
              <div class="list">
                <ul>
                  <li v-for="(item,index) in tableData" :key="item.index">{{ item.name }}</li>
                </ul>
              </div>
            </div>
            <!--             材料-->
            <p class="icon_p  mb24 mt24"><i class="red">*</i> 材料<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">PC</p>
              <p>PET</p>
            </div>
            <!--             材质-->
            <p class="icon_p  mb24 mt24"><i class="red">*</i> 材质<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">光滑表面</p>
              <p>磨砂表面</p>
            </div>
            <!--背胶-->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>  背胶：<i class="iconfont">&#xe653;</i>

            </p>
            <div class="option both gum">
              <p class="select">3M55236</p>
              <p>3M9448</p>
              <p>3M467</p>
              <p>3M468</p>
              <p>3M300LSE</p>
              <p>3M300LSE(IP66)</p>
            </div>
            <!--              基材厚度(mm)-->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>  基材厚度(mm)<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">0.175</p>
              <p>0.25</p>
            </div>

            <!--总厚度：约0.9~1.3mm（常规）-->
            <p>总厚度：约0.9~1.3mm（常规）</p>
            <p class="icon_p  mb24 mt24"><i class="red">*</i>颜色数量: <span class="f24 cl_999">注:工厂按 SET=0单片(pcs)制作</span></p>
            <div class="option both">
              <p class="select">1</p>
            </div>
            <!-- 凸点(按钮鼓包)： -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>凸点(按钮鼓包)<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p>无</p>
              <p class="select">有</p>
            </div>
            <!-- 是否有LED -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>是否有LED<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">无</p>
              <p>有</p>
            </div>
            <!-- 线路凸点要求 -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>线路凸点要求<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">银浆起凸</p>
              <p>锅仔片</p>
            </div>
            <!-- 外形切割方式 -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>外形切割方式<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">激光切割</p>
              <p>模具切割</p>
            </div>
          </div>
