          <div class="basic">
            <!-- 窗口光面其它磨砂处理： -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>窗口光面其它磨砂处理：<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">否</p>
              <p>是</p>
            </div>
            <!-- 浅色背景： -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>浅色背景：<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">否</p>
              <p>是</p>
            </div>
            <!-- 加防静电网： -->
            <p class="icon_p  mb24 mt24"><i class="red">*</i>加防静电网：<i class="iconfont">&#xe653;</i></p>
            <div class="option both">
              <p class="select">否</p>
              <p>是</p>
            </div>

          </div>


        </van-collapse-item>
      </van-collapse>
      <div class="FillOver both">
        <p>重新填写</p>
        <span @click="nextStep" :class="walk==true?'bg_FFC266':'bg_f90'">下一步</span>
      </div>


    </div>
</template>

<script>
    import Vue from "vue"
    import $ from "jquery";

    export default {
        name: "ThinFilmPanel",
        components: {},
        data() {
            return {
               activeName:['1','2','3'],
              Height:"",
              Width:"",
              walk:true,
              //模拟的selet
              tableData:[{'name':111}, {'name':222}, {'name':333}, {'name':444}],
              selectValue:"12312123",

            }
        },
        methods: {
          //时刻监听输入框宽高是否输入
          HeightWidth(){
            if(this.Height!=""&&this.Width!=""){
              this.walk=false;
            }else{
              this.walk=true;
            }

          },
          //下一步跳转
          nextStep(){
            console.log("1");
            this.$router.push({
              path:'ThinPanelParameter'
            })
          },
          Onclick(data,index){
            this.Home.forEach((item,index)=>{
              console.log(item);
              item.ThisTrue=false;
            });
            data.ThisTrue=true;
          },

        },
      mounted() {
        $(".ThinFilmPanel").on("click",function (event) {event.stopPropagation();$(this).find(".selectNumber ul").hide();});
        $(".selectNumber .input").on("click",function(event) {  $(".ThinFilmPanel").find(".selectNumber ul").hide();    $(this).parents(".selectNumber").find(".list ul").show(); event.stopPropagation(); });
        $(".selectNumber li").on("click",function(event){  event.stopPropagation(); $(this).parents(".selectNumber").find(".input p").text($(this).text()); $(this).parents("ul").hide();})
        $(".option p").click(function(){console.log("11");$(this).addClass("select");$(this).siblings().removeClass("select")});
        $(".opboxtion p").click(function(){if($(this).hasClass("select")){$(this).removeClass("select")}else{$(this).addClass("select");}})

      },
        created() {
          this.$emit('getMessage', "3");
        }
    }

</script>

<style scoped>
  .ThinFilmPanel input{padding-left: .24rem;}
  .ThinFilmPanel {padding-bottom: 1.66rem}
  .ThinFilmPanel .basic{background: #fff;}
  .ThinFilmPanel .basic .icon_p{font-size: .28rem;color: #666}
  .ThinFilmPanel .basic .red{display: inline-block;vertical-align: middle;color: red;}
  .ThinFilmPanel .icon_p .iconfont{color:  #7C5922}
  .ThinFilmPanel .option{margin-bottom: .30rem}
  .ThinFilmPanel .option p{display: inline-block;float: left;min-width: 1.54rem;height: .84rem;border: 1px solid #D1D3D8;text-align: center;line-height: .84rem;margin-right: .20rem;padding: 0 0.09rem;border-radius: 4px;color: #333 }
  .ThinFilmPanel .option p:last-child{margin-right: 0}
  .ThinFilmPanel .option .select{border: 1px solid #f90;color: #f90;}
  .ThinFilmPanel .option .NoChoose{border: 1px solid #D1D3D8;color: #999999;background: #F3F3F3}
  .ThinFilmPanel .importInput input{float: left;width: 2.77rem;height: .84rem;line-height: .84rem;border: 1px solid #D1D3D8}
  .ThinFilmPanel .importInput span{float: left;width:.66rem;height: .84rem;line-height: .84rem;text-align: center;background: #E4E4E4;display: inline-block;border-top: 1px solid #D1D3D8;border-bottom: 1px solid #D1D3D8;font-size: .28rem }
  .ThinFilmPanel .importInput span:last-child{border-right: 1px solid #D1D3D8;}
  .ThinFilmPanel .selectNumber{position: relative;width: 100%;height: .84rem;border: 1px solid #D1D3D8;}
  /*制作方式*/
  .ThinFilmPanel .selectNumber{position: relative;width: 100%;height: .84rem;border: 1px solid #D1D3D8;}
  .ThinFilmPanel .selectNumber .input{height: 100%;line-height: .8rem}
  .ThinFilmPanel .selectNumber .input p{width: 100%;border: none;display: inline-block;padding-left: .24rem}
  .ThinFilmPanel .selectNumber .input img{position: absolute;right: .24rem;top: .38rem;width: .16rem;}
  .ThinFilmPanel .selectNumber ul{width: 100%;background: #fff;position: absolute;top: .84rem;z-index: 100;    box-shadow: 0px 1px 6px 0px rgba(146, 151, 161, 0.45);display: none}
  .ThinFilmPanel .selectNumber ul li {width: 100%;height: .8rem;line-height: .8rem;padding-left: .24rem}
  /*工艺边*/
  .ThinFilmPanel .ProcessSide p{display: inline-block;float: left;width: 1.56rem;height: .84rem;line-height: .84rem;text-align: center;border: 1px solid #D1D3D8;margin-right: .2rem}
  .ThinFilmPanel .ProcessSide p:last-child{margin-right: 0}
  .ThinFilmPanel .ProcessSide .select{border: 1px solid #f90;color: #f90;}
  /*工艺宽度*/
  .ThinFilmPanel .TechnologyWide{border: 1px solid #D1D3D8}
  .ThinFilmPanel .TechnologyWide input{width: 6rem;float: left;height: .84rem;line-height: .84rem;border: none}
  .ThinFilmPanel .TechnologyWide span{width: .82rem;float: left;height: .84rem;line-height: .84rem;display: inline-block;text-align: center;background: #E4E4E4;color: #999;font-size: .28rem;border-left: 1px solid  #D1D3D8}
  /*槽间距*/
  .ThinFilmPanel .PitchGrooves div input:hover{border: 1px solid  #f90 !important;}
  .ThinFilmPanel .PitchGrooves .left{float: left}
  .ThinFilmPanel .PitchGrooves .left p{display: inline-block;vertical-align: middle;font-size: .28rem;line-height: .84rem;margin-right: .1rem}
  .ThinFilmPanel .PitchGrooves .left div input{float: left ; height: .84rem  ; width: 1.46rem;height: .84rem;line-height: .84rem;border: 1px solid #D1D3D8}
  .ThinFilmPanel .PitchGrooves .left div span{float: left ; height: .84rem  ; display: inline-block;text-align: center;width: .9rem;text-align: center;line-height: .84rem;background:#E4E4E4; border-right: 1px solid  #D1D3D8;border-top: 1px solid  #D1D3D8;border-bottom: 1px solid  #D1D3D8;}
  .ThinFilmPanel .PitchGrooves .right{float: right}
  .ThinFilmPanel .PitchGrooves .right p{display: inline-block;vertical-align: middle;font-size: .28rem;line-height: .84rem;margin-right: .1rem}
  .ThinFilmPanel .PitchGrooves .right div input{float: left ; height: .84rem  ; width: 1.46rem;height: .84rem;line-height: .84rem;border: 1px solid #D1D3D8}
  .ThinFilmPanel .PitchGrooves .right div span{ float: left ; height: .84rem  ;display: inline-block;text-align: center;width: .9rem;text-align: center;line-height: .84rem;background:#E4E4E4;border-right: 1px solid  #D1D3D8;border-top: 1px solid  #D1D3D8;border-bottom: 1px solid  #D1D3D8;}
  .ThinFilmPanel .van-collapse-item{margin-top: .24rem}
  .ThinFilmPanel .van-collapse-item .van-cell{border-bottom: 1px solid #ebedf0}
  .ThinFilmPanel .van-cell::after{border-bottom: none}
  .ThinFilmPanel .title_bg{width: 100%}
  .ThinFilmPanel .FillOver{position: fixed;bottom: 0;width: 100%;height: 1.12rem;line-height: 1.12rem;box-shadow: 0px -3px 5px 0px
  rgba(0, 0, 0, 0.12);z-index: 100}
  .ThinFilmPanel .gum p{width: 2.16rem;margin: 0;}
  .ThinFilmPanel .gum p:nth-child(-n+3){margin-bottom: .2rem;}
  .ThinFilmPanel .gum p:nth-child(3n-1){margin: 0 .15rem;}
  .ThinFilmPanel .gum p:last-child{font-size: .24rem;}
  .ThinFilmPanel .FillOver p{display: inline-block;float: left;width: 50%;text-align: center;font-size: .34rem;background: #fff;color: #f90;height: 100%}
  .ThinFilmPanel .FillOver span{display: inline-block;float: left;width: 50%;color: #fff;font-size: .34rem;text-align: center;height: 100%}
</style>



